.main-nav {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    max-width: 1280px;
    margin-left: auto;
    margin-right: auto;
    padding: 0 20px;
  }
  
  .main-nav img {
    width: 145px;
  }
  
  .main-nav ul {
    display: flex;
    margin-top: 10px;
  }
  
  .main-nav a {
    color: #ff6060;
    font-weight: 500;
    font-size: 24px;
  }
  
  .main-nav a:hover {
    text-decoration: underline;
  }
  
  .main-nav li:nth-child(2) {
    margin-left: 20px;
  }
  
  @media screen and (min-width: 768px) {
    .main-nav {
      margin-bottom: 60px;
    }
  
    .main-nav img {
      width: 210px;
    }
  
    .main-nav li {
      text-transform: none;
      font-size: 1.2rem;
    }
  
    .main-nav li:nth-child(2) {
      margin-left: 60px;
    }
  }
  