header {
    padding: 0 15px;
    max-width: 1280px;
    margin: auto;
}
.banner-cover {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
    border-radius: 25px;
  }
  
  .banner,
  .banner img {
    width: 100%;
    height: 111px;
    border-radius: 25px;
  }
  
  .banner {
    position: relative;
    margin-bottom: 20px;
  }
  
  .banner img {
    object-fit: cover;
  }
  
  .banner h2 {
    color: #fff;
    font-weight: 500;
    font-size: 1.5rem;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
  
  @media screen and (min-width: 768px) {
    .banner,
    .banner img {
      height: 223px;
    }
  
    .banner {
      margin-bottom: 40px;
    }
  
    .banner h2 {
      font-size: 2.2rem;
    }
  }
  